.myProjects{
    position: relative ;
     height: 100%;
     width: 90%;
     margin: auto;
     padding: 80px 0;
     
}



.card-body{
    box-shadow: 0 0 20px 7px rgba(0, 0, 0, 1);
}

.card{
    margin: 20px 10px;
}

.card-body{
    color: #0c7f90;
    text-decoration: none;
    font-size: 15px;
    margin-top: auto;
    font-weight: 600;
    align-items: center;
   
}

.card-body a:hover{
    color: #63a6b0;
}


.card-image-cart{
    height: 30%;
    width: 30%;
    margin-bottom: 40px;
}

.card-image-mosip{
    height: 24%;
    width: 24%;
    margin-bottom: 20px;
}

.card-image-spring{
    height: 30%;
    width: 30%;
    margin-bottom: 35px;
}

.col-md-4{
    filter: brightness(0.5) grayscale(100);
    transition: 0.3s ease-in-out;
}
.col-md-4:hover{
    filter: brightness(1) grayscale(0);
}