@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

    



.AboutMe{
  
    height: auto;
    color: #fff;
    position: relative;
    min-height: -webkit-fill-available;
    
}

.main-info{
    /* font-size: 5rem; */
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    letter-spacing: 2px;
    top: 30% !important ;
    padding: 10rem 5rem;
}

.main-intro{
   display: block;
  font-size: 3rem;
  text-decoration: none !important;
  color: #333;
  letter-spacing: 2px; 
}

.main-intro p{
  
  color: white;
  
}
.hoverButton{
    display: block;
    width: 200px;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    font-family: sans-serif;
    text-decoration: none !important;
    color: #333;
    border: 2px solid #333;
    letter-spacing: 2px;
    text-align: center;
    position: relative;
    transition: all .35s;
  }
  
  .hoverButton span{
    position: relative;
    z-index: 2;
  }
  
  .hoverButton:after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #07d86f;
    transition: all .35s;
  }
  
  .hoverButton:hover{
    color: #fff;
  }
  
  .hoverButton:hover:after{
    width: 100%;
  }

  .contactMe{
    border-top: 1px solid white;
    text-align: center;
  }
  
  .brand-icon{
    margin-top: 10px;
    width: 20px !important;
    height: 30px !important;
    margin-right: 2rem;
   margin-left: 2rem;
   cursor: pointer;
   filter: brightness(0.5) grayscale(100);
    transition: 0.3s ease-in-out;
  }

  .brand-icon:hover{
    filter: brightness(1) grayscale(0);
  }
 