.hoverButton{
    display: block;
    width: 200px;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    font-family: sans-serif;
    text-decoration: none !important;
    color: #333;
    border: 2px solid #333;
    letter-spacing: 2px;
    text-align: center;
    position: relative;
    transition: all .35s;
  }
  
  .hoverButton span{
    position: relative;
    z-index: 2;
  }
  
  .hoverButton:after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #07d86f;
    transition: all .35s;
  }
  
  .hoverButton:hover{
    color: #fff;
  }
  
  .hoverButton:hover:after{
    width: 100%;
  }

