.contact-main{
    position: relative ;
    height: 100%;
    width: 100%;
    margin: auto;
    padding: 100px 0;
    display: flex;
    /*  background: #ddd; */
}


.contact-main h1{
    text-align: center;
    color:white
}
.personal-info{
    width: 50%;
    color:white;
    /* border: 2px solid white; */
}

.personal-card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    /* max-width: 300px; */
    /* margin: 180px ; */
    padding-top: 200px;
    text-align: center;
    font-family: arial;
    background: black;
  }

 .personal-val{
    filter: brightness(0.5) grayscale(100);
    transition: 0.3s ease-in-out;
    margin-left: 2px ;
 
 }

 .personal-val:hover{
    filter: brightness(1) grayscale(0);
  }

  .personal-val-mail{
    filter: brightness(0.5) grayscale(100);
    transition: 0.3s ease-in-out;
    margin-left: 2px ;
 }

 .personal-val-mail:hover{
    filter: brightness(1) grayscale(0);
  }

  .personal-val-phone{
    filter: brightness(0.5) grayscale(100);
    transition: 0.3s ease-in-out;
    margin-left: 1px ;
 }

 .personal-val-phone:hover{
    filter: brightness(1) grayscale(0);
  }
 

  .profile-icon{
    /* margin-top: 10px; */
    width: 20px !important;
    height: 30px !important;
    margin-right: 2rem;
    cursor: pointer;
   
  }

 


.contact-section{
    width: 50%;
    /* border: 2px solid white; */
}

.contact-section h1{
    text-align: center;
    color:white
}

.border{
    width: 100px;
    height: 10px;
    background: #01180c;
    margin: 40px auto;

}

.contact-form{
    max-width: 600px;
    margin: auto;
    padding: 0 10px;
    overflow: hidden;
   
}

.contact-form-text{
    display: block;
    width: 100%;
    box-sizing: border-box;
    margin: 16px 0;
    border: 0;
    background: #012211;
    padding: 20px 40px;
    outline: none;
    color: #ddd;
    transition: 0.5s;

}

.contact-form-text:hover{
    box-shadow: 0 0 10px 4px #07d86f;
}

textarea.contact-form-text{
    resize: none;
    height: 120px;
}

.contact-form-btn{
    float: right;
    border: 0;
    background: #012211;
    color: #fff;
    padding: 12px 50px;
    border-radius: 20px;
    cursor: pointer;
    transition: 0.5s;
}

.contact-form-btn:hover{
    background: #07d86f;
}