.main{
    position: relative ;
    height: 100%;
    width: 90%;
    margin: auto;
    padding: 100px 0;
}
.about-content{
    margin-top: 45px;
    text-align: left;
    /* border: 2px solid white; */
    width: 60%;
    
}

.main-content{
    display: flex;
}

.timeline-container {
    min-height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    /* border: 2px solid white; */
    margin-top: 200px;
  }
  .timeline {
    width: 80%;
    height: auto;
    max-width: 800px;
    margin: 0 auto;
    position: relative;
    width: fit-content;
  }
  
  .timeline ul {
    list-style: none;
  }
  .timeline ul li {
    padding: 20px;
    background-color: #1e1f22;
    color: white;
    border-radius: 10px;
    margin-bottom: 20px;
  }
  .timeline ul li:last-child {
    margin-bottom: 0;
  }
  .timeline-content h1 {
    font-weight: 500;
    font-size: 25px;
    line-height: 30px;
    margin-bottom: 10px;
  }
  .timeline-content p {
    font-size: 16px;
    line-height: 30px;
    font-weight: 300;
  }
  .timeline-content .date {
    font-size: 12px;
    font-weight: 300;
    margin-bottom: 10px;
    letter-spacing: 2px;
  }
  @media only screen and (min-width: 768px) {
    .timeline:before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 2px;
      height: 100%;
      background-color: gray;
    }
    .timeline ul li {
      width: 50%;
      position: relative;
      margin-bottom: 50px;
    }
    .timeline ul li:nth-child(odd) {
      float: left;
      clear: right;
      transform: translateX(-30px);
      border-radius: 20px 0px 20px 20px;
    }
    .timeline ul li:nth-child(even) {
      float: right;
      clear: left;
      transform: translateX(30px);
      border-radius: 0px 20px 20px 20px;
    }
    .timeline ul li::before {
      content: "";
      position: absolute;
      height: 20px;
      width: 20px;
      border-radius: 50%;
      background-color: gray;
      top: 0px;
    }
    .timeline ul li:nth-child(odd)::before {
      transform: translate(50%, -50%);
      right: -30px;
    }
    .timeline ul li:nth-child(even)::before {
      transform: translate(-50%, -50%);
      left: -30px;
    }
    .timeline-content .date {
      position: absolute;
      top: -30px;
    }
    .timeline ul li:hover::before {
      background-color: #07d86f;
    }
  }
  
  .main-skills{
    margin-top: 45px;
    margin-left: 20px;
    text-align: left;
    height: 400px;
    width: 40%;
    color: white;
    /* border: 2px solid white; */
      /* margin-left: 100px; */
  }

  .main-skills li{
      list-style: none;
      text-decoration: none !important;
            margin: 20px 0;
  }

  .bar{
      background: #07d86f;
      display: block;
      height: 6px;
      border: 1px solid rgba(0, 0, 0, 0.3);
      border-radius: 3px;
      overflow: hidden;
      box-shadow: 0 0 10px #0f7040;
  }

  .bar span {
      height: 6px;
      float: left;
      background: #012b16;
  }

  .JAVA{
      width: 90% ;
      animation: java 2s;
  }

  .Node{
      width: 70%;
      animation: Node 2s;
  }

  .Angular{
      width: 60%;
      animation: Angular 2s;
  }

  .React{
      width: 75%;
      animation: React 2s;
  }

  .HtmlCss{
    width: 70%;
    animation: HtmlCss 2s;
  }

  @keyframes java{
      0%{
          width: 0%;
      }
      100%{
          width: 90%;
      }
  }

  @keyframes Node{
    0%{
        width: 0%;
    }
    100%{
        width: 70%;
    }
}
@keyframes Angular{
    0%{
        width: 0%;
    }
    100%{
        width: 60%;
    }
}
@keyframes React{
    0%{
        width: 0%;
    }
    100%{
        width: 75%;
    }
}
@keyframes HtmlCss{
    0%{
        width: 0%;
    }
    100%{
        width: 70%;
    }
}